<template>
  <div id="item-comparator">
    <div>
      <v-layout>
        <v-flex xs1>
          <v-chip
            v-for="(v,i) in cardNum"
            :color="curCardIndex === i ? 'blue' : items[i] ? 'primary' : 'secondary'"
            :key="i"
          ></v-chip>
        </v-flex>
        <v-flex xs2>
          <strong>Sort by &nbsp;</strong>
          <v-select
            v-model="sortBy"
            single-line
            dense
            flat
            class="d-inline-block"
            :items="sortOptions"
            @input="generateSortedList"
          ></v-select>
        </v-flex>
        <v-flex xs2>
          <v-text-field
            v-model="search"
            append-icon="search"
            label="Search the key or value"
            single-line
            hide-details
            hide-no-data
          ></v-text-field>
        </v-flex>
        <v-flex xs2>
          <v-checkbox label="Hide totally different" v-on:change="filterDifferent" />
        </v-flex>
      </v-layout>
      <!-- Caption -->
      <v-layout row wrap>
        <v-flex xs4 class="text-xs-left">
          <strong>Different</strong>
        </v-flex>
        <v-flex xs4 class="text-xs-center">
          <strong>Somewhat different</strong>
        </v-flex>
        <v-flex xs4 class="text-xs-right">
          <strong>Same</strong>
        </v-flex>
      </v-layout>
      <div ref="legend"></div>
    </div>

    <v-card>
      <v-card-text id="card-contents">
        <!-- :options="{handle:'#index'}" -->
        <draggable :list="list">
          <v-list v-for="value in filteredData" :key="value[0]">
            <v-layout row wrap justify-space-between>
              <v-flex xs12 class="mb-2">
                <v-divider></v-divider>
              </v-flex>

              <v-flex xs1 align-self-center>
                <p id="index">{{ value[0] }}:</p>
                <DiffComparator v-bind:itemsDiff="value[cardNum + 1]" v-bind:gradient="gradient"></DiffComparator>
              </v-flex>
              <v-divider vertical></v-divider>

              <v-flex xs7 align-self-center>
                <urlComparator v-if="isUrl(value[1])" v-bind:values="value.slice(1, cardNum + 1)" />
                <longTextComparator
                  v-else-if="isLongtext(value[1], value[0])"
                  v-bind:values="value.slice(1, cardNum + 1)"
                  v-bind:curIndex="curCardIndex"
                />
                <imageComparator
                  v-else-if="isImage(value[1], value[0])"
                  v-bind:values="value.slice(1, cardNum + 1)"
                />
                <listComparator
                  v-else-if="isArray(value[1], value[0])"
                  v-bind:values="value.slice(1, cardNum + 1)"
                  v-bind:curIndex="curCardIndex"
                />
                <normalComparator
                  v-else
                  v-bind:values="value.slice(1, cardNum + 1)"
                  v-bind:curIndex="curCardIndex"
                />
              </v-flex>
              <v-divider vertical></v-divider>
              <v-flex xs3>
                <Distribution
                  v-bind:values="value.slice(1, cardNum + 1)"
                  v-bind:descriptor="getdescriptors(value[0])"
                />
              </v-flex>
            </v-layout>
          </v-list>
        </draggable>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import * as d3 from "d3";
import { schemeGnBu } from "d3-scale-chromatic";
import draggable from "vuedraggable";

import DiffComparator from "./ColorEncodingComparator/Comparator";
import loadedComparators from "./comparison/compare";
import urlComparator from "./Card_cell_comparator/urlComparator";
import imageComparator from "./Card_cell_comparator/imageComparator";
import longTextComparator from "./Card_cell_comparator/longTextComparator";
import listComparator from "./Card_cell_comparator/listComparator";
import normalComparator from "./Card_cell_comparator/normalComparator";
import Distribution from "./Card_cell_comparator/Distribution";

/*

  Card component expects 2 props:
    columnTypes: an array of the column types(string types), each string value should be "String" or "Number"
    leftItem: an array of key-value pair, (key-value pair is an array with index 0 being the key, index 1 being the pair)
      for one row data that is shown on the left
    rightItem: the data structure should be the same as the leftItem

*/

export default {
  name: "Card",
  components: {
    DiffComparator,
    draggable,
    urlComparator,
    imageComparator,
    longTextComparator,
    listComparator,
    normalComparator,
    Distribution
  },
  props: ["numCard", "descriptors"],

  data() {
    return {
      gradient:
        "to right, #f7fcf0, #e0f3db, #ccebc5, #a8ddb5, #7bccc4, #4eb3d3, #2b8cbe, #0868ac, #084081",
      search: "",
      sortBy: "default",
      sortOptions: [
        "Default",
        "Similarity",
        "Key",
        "Value"
        // "String",
        // "Longtext",
        // "Numerical",
        // "Categorical",
        // "Set",
        // "Image"
        // "List"
      ],
      compareOptions: [
        "String",
        "Longtext",
        "Numerical",
        "Categorical",
        "Set",
        "Image"
        // "List"
      ],

      list: null, // the list that is shown, initialized from "joinedArray" props
      upSort: false,
      joinedArray: null,
      dis: [],
      cardNum: 2,
      curCardIndex: -1, // the first data loads in will increment this to one
      items: []
    };
  },
  computed: {
    filteredData() {
      if (!this.search) return this.list;
      const regex = new RegExp(this.search, "i");

      return this.list.filter(
        item => {
          return item.some((e, i) => {
            if (i < this.cardNum + 1) return regex.test(e);
            else return false;
          });
        }
        // regex.test(item[0]) || regex.test(item[1]) || regex.test(item[2])
      );
    },
    isFilled() {
      return this.items.length >= this.cardNum;
    },
    isCleared() {
      return this.items.length === 0;
    }
  },
  methods: {
    // Clear the saved list
    createLegend() {
      let width = 300;
      let height = 10;
      let gradientScheme = schemeGnBu[9];
      const svg = d3
        .select(this.$refs.legend)
        .append("svg")
        .attr("preserveAspectRatio", "xMinYMin meet")
        .attr("viewBox", `0 0 ${width} ${height}`);
      let legend = svg
        .append("defs")
        .append("svg:linearGradient")
        .attr("id", "gradient")
        .attr("x1", "0%")
        .attr("y1", "100%")
        .attr("x2", "100%")
        .attr("y2", "100%")
        .attr("spreadMethod", "pad");
      for (let i = 0; i < 9; i++) {
        legend
          .append("stop")
          .attr("offset", `${Math.round(((i + 1) / 9.0) * 100) / 100}`)
          .attr("stop-color", gradientScheme[i])
          .attr("stop-opacity", 1);
      }
      svg
        .append("rect")
        .attr("width", width)
        .attr("height", height)
        .style("fill", "url(#gradient)");
    },

    filterDifferent(flag) {
      if (flag) {
        this.list = this.list.filter(e => e[this.cardNum + 1] > 0);
      } else {
        this.list = this.joinedArray.slice();
      }
    },

    // Add key values as an object to list and sort
    generateSortedList(option) {
      let comparison = new loadedComparators();

      this.upSort = !this.upSort;
      this.sortBy = option;

      if (this.sortBy === "Default") {
        this.list = this.joinedArray.slice();
        return;
      }
      let lambda;
      switch (this.sortBy) {
        case "Key":
          lambda = (a, b) => {
            let result = a[0] > b[0] ? 1 : -1;
            return this.upSort ? result : -result;
          };
          break;
        case "Value":
          lambda = (a, b) => {
            let result = a[1] > b[1] ? 1 : -1;
            return this.upSort ? result : -result;
          };
          break;
        case "Similarity":
          lambda = (a, b) => {
            let result = a[this.cardNum + 1] - b[this.cardNum + 1];
            return this.upSort ? result : -result;
          };
          break;
        default:
          lambda = (a, b) => {
            let result = this.compareFunction(a[1], b[1], option, comparison);
            return this.upSort ? result : -result;
          };
          break;
      }
      this.list.sort((a, b) => lambda(a, b));
    },
    compareFunction(a, b, option, comparison) {
      if (a === null || b === null) return 0;
      let result = 0;
      switch (option) {
        case "Numerical":
          a = Number(a);
          b = Number(b);
          if (isNaN(a) || a === null || isNaN(b) || b === null) return 0;
          result = comparison.compare(option, a, b);
          break;
        case "Categorical":
        case "Longtext":
        case "String":
        case "Image":
          a = String(a);
          b = String(b);
          if (a.length === 0 || a === null || b.length === 0 || b === null)
            return 0;
          result = comparison.compare(option, a, b);
          break;
        case "Set":
          result = comparison.compare(option, a, b);
          break;
      }
      return result;
    },
    generateUnsortedList() {
      const comparator = new loadedComparators();
      let values = this.items[0].map(
        (e, i) => {
          const descriptor = this.descriptors.filter(
            descriptor => descriptor.name === e[0]
          )[0];

          // Try to find the default factory function
          if (this.compareOptions.includes(descriptor.type)) {
            return this.compareFunction(
              e[1],
              this.items[1][i][1],
              descriptor.type,
              comparator
            );
          } else {
            if (e[1] === null || this.items[1][i][1] === null) return 0;

            // Could not find the default factory function, naive check if same
            return e[1] === this.items[1][i][1] ? 1 : 0;
          }
        } // Index 0 is the key, index 1 is the value for each item
      );

      let joinedArray = new Array(this.items[0].length);
      for (let i = 0; i < joinedArray.length; i++) {
        joinedArray[i] = new Array(2 * this.cardNum);
        // Get the key for the joined array
        joinedArray[i][0] = this.items[0][i][0];

        for (let j = 0; j < this.cardNum; j++) {
          // get the value
          joinedArray[i][j + 1] = this.items[j][i][1];
          // joinedArray[i][2] = this.items[1][i][1];
          // joinedArray[i][3] = this.items[2][i][1];
        }

        joinedArray[i][this.cardNum + 1] = values[i];
      }

      this.joinedArray = joinedArray;
      this.list = joinedArray.slice();

      // Generate the difference value for each field
      // this.list = this.list.map((e, i) => {
      //   e[3] = comparator.compare(this.types[i], e[1], e[2]);
      //   //Create values in the end for each field
      //   return e;
      // });
    },
    isImage(url, value) {
      if (this.descriptors) {
        let descriptors = this.descriptors.filter(e => e.name === value)[0];
        if (descriptors) {
          return descriptors.type === "Image";
        } else {
          return false;
        }
      }

      if (!url || typeof url != "string") return false;
      return url.match(/\.(jpeg|jpg|gif|png)$/) != null;
    },
    isLongtext(str, value) {
      if (this.descriptors) {
        let descriptors = this.descriptors.filter(e => e.name === value)[0];
        if (descriptors) {
          return descriptors.type === "Longtext";
        } else {
          return false;
        }
      }
      if (!str) return false;
      return str.length > 100;
    },
    isUrl(str) {
      const pattern = new RegExp(
        "^(http?:\\/\\/)?" + // protocol
        "((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|" + // domain name
        "((\\d{1,3}\\.){3}\\d{1,3}))" + // OR ip (v4) address
        "(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*" + // port and path
        "(\\?[;&a-z\\d%_.~+=-]*)?" + // query string
          "(\\#[-a-z\\d_]*)?$",
        "i"
      );
      return !!pattern.test(str);
    },
    isArray(v, value) {
      if (this.descriptors) {
        let descriptors = this.descriptors.filter(e => e.name === value)[0];
        if (descriptors) {
          return descriptors.type === "List";
        } else {
          return false;
        }
      }
      return Array.isArray(v);
    },
    getdescriptors(value) {
      if (this.descriptors) {
        return this.descriptors.filter(e => e.name === value)[0];
      }
    },
    addItem(row) {
      this.curCardIndex = ++this.curCardIndex % this.cardNum;
      if (this.items.length === this.cardNum) {
        this.items[this.curCardIndex] = Object.entries(row);
      } else {
        this.items.push(Object.entries(row));
      }

      if (this.isFilled) {
        try {
          this.generateUnsortedList();
          this.sortBy = "default";
        } catch (error) {
          const msg =
            "Failed to add item, did you not provide the descriptors or add an item from different datasets?";
          console.error(msg, error);
        }
      }
    }
  },
  mounted() {
    this.createLegend();
    if (this.numCard) {
      // If the user passes in a different value for number of cards
      this.cardNum = this.numCard;
    }
  },
  watch: {
    items: {
      handler() {
        //   if (this.isCleared) {
        //     this.list = [];
        //     return;
        //   }
        //   if (this.isFilled) {
        //     this.generateUnsortedList();
        //     this.sortBy = "default";
        //   }
      }
      // immediate: true // To invoke
    }
  }
};
</script>

<style scoped>
h1,
h2 {
  font-weight: normal;
}
label {
  text-align: left;
}
ul {
  list-style-type: none;
  padding: 0;
}
.v-list {
  display: inline-flex;
}

v-card {
  margin: 10px;
}
#card-contents {
  text-align: left;
}
#card-contents .v-list {
  display: flex;
}
a,
#index {
  word-wrap: break-word;
  color: #42b983;
  font-weight: bold;
  font-family: courier;
}
.transition-ease-in {
  transition: transform 50ms ease-in-out;
}
v-textarea {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
.v-btn--icon {
  border: 1px solid black;
}
</style>