<template>
  <v-app>
    <v-container fluid ma-0 pa-0>
      <v-layout row wrap>
        <v-flex xs12>
          <AgGrid
            v-bind:headers="head"
            v-bind:data="file"
            v-bind:descriptors="descriptor"
            v-on:preview-row="previewRow"
          />
        </v-flex>
        <!-- <v-select
          v-model="select"
          :items="items"
          item-text="dataName"
          item-value="path"
          label="test dataset"
          return-object
          single-line
        ></v-select>-->
      </v-layout>
      <!-- v-bind:newCardIndex="newCardIndex" -->

      <v-flex xs12 grow pa-1>
        <Card ref="ItemComparator" v-bind:descriptors="descriptor" v-bind:numCard="2" />
      </v-flex>
    </v-container>
  </v-app>
</template>

<script>
import Card from "./components/CardComparator/Card";
import AgGrid from "./components/AgGrid/AgGrid";
import createFileDescriptor from "./createFileDescriptor";
import fileDescriptor from "./../public/FileDescriptors";

import * as Papa from "papaparse";

export default {
  name: "App",
  components: {
    Card,
    AgGrid
  },
  methods: {
    addRow(row) {
      this.$refs.ItemComparator.addItem(row);
    },
    previewRow(row) {
      this.addRow(row);
    },
    loadDescriptor(file) {
      this.descriptor = createFileDescriptor(file, fileDescriptor);
    },
    loadFile() {
      if (!this.fileurl) {
        console.error(
          "Invalid fileurl, please check the file path is defined correctly."
        );
        return;
      }
      Papa.parse(this.fileurl, {
        download: true,
        header: true,
        skipEmptyLines: true,
        dynamicTyping: true,
        complete: results => {
          const keys = Object.keys(results.data[0]); // Grab the first row to infer the type
          // Ag-grid columns
          this.head = keys;
          this.file = results.data; // Ag-grid data
          this.loadDescriptor(this.file);
        }
      });
    }
  },
  data() {
    return {
      file: [],
      head: [],
      items: [
        { dataName: "conference_data", path: "./conference_data.csv" },
        { dataName: "image_data", path: "./met_data_tiny.csv" },
        { dataName: "movie_data", path: "./movie_metadata.csv" },
        { dataName: "house_data", path: "./ame.csv" }
      ],
      select: { dataName: "conference_data", path: "./conference_data.csv" },
      flattens: [],
      descriptor: []
    };
  },
  mounted() {
    this.loadFile();
  },
  computed: {
    fileurl() {
      const url = new URL(window.location);
      const path = url.searchParams.get("file");
      if (!path) return this.select.path;
      return path;
    }
  },
  watch: {
    fileurl() {
      // this.loadFile();
      // Clear out data in viewer
    }
  }
};
</script>


