<template>
  <v-layout>
    <v-flex xs1>
      <OverlayScrollbar
        v-bind:stepSize="stepSize"
        v-bind:dataLen="data.length"
        v-bind:highlightedIndices="sampledIndices"
        v-bind:highlightedIndicesColors="sampledIndicesColors"
        v-bind:bucketIndices="bucketIndices"
      ></OverlayScrollbar>
    </v-flex>
    <v-flex xs11>
      <ag-grid-vue
        v-model="rowData"
        :gridOptions="gridOptions"
        :columnDefs="columnDefs"
        :rowSelection="rowSelection"
        :sortingOrder="sortingOrder"
        :postSort="postSort"
        :headerHeight="headerHeight"
        @gridReady="onGridReady"
        @rowDoubleClicked="onRowDoubleClicked"
        @selection-changed="onSelectionChanged"
        @sortChanged="onSortChanged"
        @firstDataRendered="OnfirstDataRendered"
        style="height: 100vh;"
        class="ag-theme-balham"
        animateRows
        pagination
        paginationAutoPageSize
        suppressScrollOnNewData
        rowDeselection
        suppressColumnVirtualisation
        suppressPaginationPanel
      ></ag-grid-vue>
    </v-flex>
  </v-layout>
</template>

<script>
import { AgGridVue } from "ag-grid-vue";
import * as d3 from "d3";
import tableschema from "tableschema";

import { createHeaderDistribution } from "./helper/createHeaderDistribution";
import { StringComparator, NumberComparator } from "./helper/custom-comparator";

import CustomHeader from "./CustomHeader";
import ToolbarHeader from "./ToolbarHeader";
import OverlayScrollbar from "./OverlayScrollbar";

/*
  "Ag-grid" component expects 3 props:
    header: an array of the column name
    data: an array of key-value pair for the whole row data (key-value pair is an array with index 0 being the key, index 1 being the pair)
      the order should match the header order. 
    descriptors: an array of object that describes all column, the order does not need to match the order in the header, but should provide
      a "name" field 

  "Ag-grid" component emits 2 events:
    "preview-row": emits whenever the users select a row, the payload is an array of length 1 with the first item being the selected row.
    "sampledIndices-change": [WARNING] This feature is not completed yet. It will only emit when new data loads in the ag-grid. The payload is 
      the rows being shown on the first page

*/
export default {
  name: "AgGrid",
  props: ["headers", "data", "descriptors"], // original data
  components: {
    AgGridVue,
    OverlayScrollbar
  },
  data() {
    return {
      // ------ Grid parameters ------
      gridOptions: {},
      columnDefs: null, // initialized column behaviors for every column based on "columnTypes" props when the "header" props load in
      rowData: null, // data for display, original data comes from "data" props
      rowSelection: "single",
      sortingOrder: ["asc", "desc"],
      headerHeight: 150,

      // ------ Custom parameters ------
      dataBackground: [], // current dataset, used to indicate the position after sorting (focusing on one column)
      rowHeight: 30, // Default row height
      rowHeights: [5, 10, 25, 30, 50],
      maxVec: {}, // stores every max key value pair of the data table
      minVec: {}, // stores every min key value pair of the data table
      gridHeight: 0, // only concerns the height where the data could be shown (excluding header and horizontal scrollbar)
      rowsNum: 0, // An estimate of how many rows does each page have
      sampleFunction: () => {}, // higher-order function for sampling, could be configurated in the future to support external sampling function
      sampledIndices: [],
      sampledIndicesColors: [],
      bucketIndices: [],
      schemaSelected: null,
      columnSelected: null,
      color: () => {}, // a d3 function that takes index as parameter and returns a hex number
      history: [], // stores the history of the data displayed to the user
      currentHistoryIndex: -1, // the start index of the history will be 0 once the grid init,
      missingDescriptor: [],
      headersInAgGridFormat: [],
      completedDescriptors: [] // original descriptors from users plus inferred ones by the library
    };
  },

  beforeMount() {},
  // created() {
  //   document.addEventListener(
  //     "mousewheel",
  //     e => {
  //       if (!this.gridApi) return; // if the ag-grid is not ready yet
  //       if (!e.ctrlKey) return; // if ctrl is not being held when scrolling, the scrolling behaves normally
  //       e.preventDefault(); // disable the scrollbar behavior
  //       let rowsSelected = this.gridApi.getSelectedRows();
  //       if (rowsSelected.length !== 0) {
  //         // if ctrl + scrolling but one row is selected
  //         let index = this.data.indexOf(this.gridApi.getSelectedRows()[0]); // index of selected row
  //         if (index !== this.firstItemIndex) {
  //           this.sampleOnRange(index);
  //         } else {
  //           this.scrollChangeRowHeight(e);
  //         }
  //       } else {
  //         this.scrollChangeRowHeight(e);
  //       }
  //     },
  //     { passive: false }
  //   );
  // },

  computed: {
    stepSize() {
      return Math.ceil(this.data.length / this.rowsNum);
    }
  },
  mounted() {},
  watch: {
    rowsNum() {},
    headers() {
      this.headersInAgGridFormat = this.headers.map(h => {
        // The passed in value should in this format for ag-grid to use it properly
        return { headerName: h, field: h };
      });

      const descriptorNames = this.descriptors.map(e => e.name);
      const missingDescriptor = this.headers.filter(
        header => !descriptorNames.includes(header)
      );

      if (missingDescriptor.length > 0) {
        // There is some missing information in the descriptor, needs to infer the data type
        console.error(
          "Missing descriptors for the following columns\n",
          missingDescriptor
        );
        this.missingDescriptor = missingDescriptor;
      }
    },

    rowData() {
      this.rowData.forEach(row => {
        if (row) row["TOOLBAR_COLUMN"] = ""; // create a empty cell for rendering the color legend
      });
    },
    async data() {
      // Async function because we need to wait for the infering type function
      this.columnSelected = null; // Clear the previous selected column
      this.rowData = this.data.slice(0); // deep copy the original data
      this.dataBackground = this.data.slice(0);

      for (const key in Object.keys(this.headers)) {
        this.maxVec[key] = -Infinity;
        this.minVec[key] = Infinity;
      }

      const headerAndRowData = [];
      headerAndRowData.push(this.headers);
      this.rowData.forEach(datum => {
        headerAndRowData.push(Object.values(datum));

        // Calculate each column minimum and maximum value
        this.headers.forEach(header => {
          const key = header;
          const vec = datum[key];
          if (typeof vec == "number") {
            this.maxVec[key] = Math.max(this.maxVec[key], Number(vec));
            this.minVec[key] = Math.min(this.minVec[key], Number(vec));
          } else if (typeof vec == "string") {
            this.maxVec[key] = Math.max(this.maxVec[key], vec.length);
            this.minVec[key] = Math.min(this.minVec[key], vec.length);
          } else {
            this.maxVec[key] = Math.max(this.maxVec[key], vec);
            this.minVec[key] = Math.min(this.minVec[key], vec);
          }
        });
      });

      if (this.missingDescriptor.length > 0) {
        let inferedDataType = await this.inferingType(headerAndRowData);

        const descriptors = this.descriptors.slice();
        this.missingDescriptor.forEach(descriptorName => {
          descriptors.push({ name: descriptorName });
        });

        const newDescriptors = descriptors.map(descriptor => {
          if (!this.missingDescriptor.includes(descriptor.name)) {
            return descriptor;
          } else {
            let comparator = null,
              type = null;

            type = inferedDataType.filter(obj => {
              return obj.name === descriptor.name;
            })[0].type;

            // The inferred data type format is different from what we used, so we do the data cleaning here

            if (type === "string") {
              type = "String";
            } else {
              type = "Number";
            }

            if (descriptor.comparator !== undefined) {
              comparator = descriptor.comparator;
            } else {
              comparator =
                type === "String" ? StringComparator : NumberComparator;
            }

            return {
              name: descriptor.name,
              type: type,
              comparator: comparator
            };
          }
        });
        this.completedDescriptors = newDescriptors;
      } else {
        // There is no missing descriptor
        this.completedDescriptors = this.descriptors;
      }

      this.createColumnDefs(
        this.headersInAgGridFormat,
        this.completedDescriptors
      );
    }
  },

  methods: {
    async inferingType(inferedData) {
      const schema = await tableschema.Schema.load({});
      schema.infer(inferedData);
      return schema.descriptor.fields;
    },

    createColumnDefs(headers, descriptors) {
      this.columnDefs = headers.map((header, i) => {
        const descriptor = descriptors.filter(x => x.name === header.field)[0];
        // This descriptor could be inferred and has only only name, type. and comparator as attributes

        return {
          ...header, // all original elements from header
          sortable: true,
          resizable: true,
          suppressMovable: true, // unable to move this column
          width: 350,
          filter:
            descriptor.type === "String"
              ? "agTextColumnFilter" // Ag-grid built-in string filter
              : "agNumberColumnFilter", // Ag-grid built-in number filter
          // menuTabs: ["filterMenuTab"],
          comparator: descriptor.comparator,
          cellClassRules: {
            "column-highlight": params =>
              params.colDef.field === this.columnSelected // To determine if it is highlight sorted column
          },
          cellRenderer: params => {
            // The return type for this function should be a html string that defines the structure of each cell
            // If the rowHeight is grater than 25, display text with sparkline visualization
            // If the rowHeight is 25, display both text and color visualization
            // If the rowHeight is smaller than 10, display only color visualization

            if (params.value === undefined || params.value === null) return ""; // edge cases

            if (descriptor.type === undefined || descriptor.type === null)
              return params.value; // When the grid does not have enough information to draw any visualization

            let width = 0;
            let range = this.maxVec[header.field] - this.minVec[header.field]; // Now the range is from Min to Max

            // If the range is not valid, return the value without visualization
            if (range === Infinity || isNaN(range)) return params.value;

            if (descriptor.type === "Categorical" && descriptor.bucketName) {
              // if type is categorical, there is no range, so we divide the cell width evenly and color encode them

              let index = descriptor.bucketName.indexOf(params.value);
              if (index !== -1) {
                width = Math.floor(
                  index * (100 / descriptor.bucketName.length)
                );
              }
            } else {
              if (!isNaN(params.value) || params.value.length === 0) {
                width = Math.floor(
                  ((params.value - this.minVec[header.field]) / range) * 100
                );
              } else {
                width = Math.floor(
                  ((params.value.length - this.minVec[header.field]) / range) *
                    100
                );
              }
            }

            width = Math.max(1, width); // if the width is 0%, it will not show properly

            if (this.rowHeight > 25) {
              // only display the sparkline visualization if the rowheight is greater than 25
              const color = this.color(params.rowIndex);
              return `
                <span class="sparkline-container">
                  ${params.value}
                </span>
                <hr class="sparkline" width="${width}%;"
                  style="border-bottom: 2px solid ${color}; transform: translateY(-2px);" >
                `;
            }

            // If the row height is too small,
            // the text will be hidden automatically by the grid
            if (!isNaN(params.value) || params.value.length === 0) {
              return `
              <div class="div-percent-bar" style="width: ${width}%; background-color: rgb(244, 67, 54);">
                ${params.value}
              </div>
            `;
            } else {
              return `
              <div class="div-percent-bar" style="width: ${width}%; background-color: grey;">
                ${params.value}
              </div>
            `;
            }
          },
          headerComponentFramework: CustomHeader,
          headerComponentParams: { index: i }
        };
      });
      // Toolbar columnDef in the 0th index, so we make one extra columnDef in the front of the array
      this.columnDefs.unshift({
        headerName: "", // the first column does not require a display name
        field: "TOOLBAR_COLUMN", // Hard-coded column name for the cell to associate with header
        // lockPosition: true, // cannot be moved
        width: 30,
        cellRenderer: params => {
          const color = this.color(params.rowIndex);
          if (this.rowHeight > 25) {
            return `<div style="background-color: ${color}; width: 30px; height: 100%; transform: translate(-15px);"></div>`;
          } else {
            return ""; // if the row height is slower than 25, hide the color visualization in the toolbar
          }
        },
        headerComponentFramework: ToolbarHeader,
        headerComponentParams: { columns: this.headers }
      });
    },

    onGridReady(params) {
      // Init ag-grid parameters
      this.gridApi = params.api;
      // this.gridApi.sizeColumnsToFit();
      this.columnApi = params.columnApi;
      this.gridOptions.api.enableSorting = true;
      this.gridOptions.rowHeight = this.rowHeight;
    },
    OnfirstDataRendered() {
      this.gridHeight = document.querySelector(
        ".ag-center-cols-clipper"
      ).clientHeight;
      // When the data first loads in, filter first
      this.rowsNum = this.gridOptions.api.paginationGetPageSize();

      let bucketIndices = [];
      for (let i = 0; i < this.rowsNum; i++) {
        bucketIndices.push(this.stepSize * i);
      }
      this.bucketIndices = bucketIndices;

      // let stepSize = Math.ceil(this.data.length / this.rowsNum);

      // Default sample function
      this.sampleFunction = stepSize => {
        return data => {
          return data.filter((x, i) => i % stepSize === 0);
        };
      };
      this.updateD3Color();
      this.updateRowDataAccordingToHeight();
    },
    onSortChanged() {
      // This method fires off after post sort
      let column = this.gridOptions.api.getSortModel()[0];
      if (!column) return;
      let upSort = column.sort === "asc";
      this.columnSelected = column.colId;
      if (upSort) {
        this.rowData = this.dataBackground.slice(0, this.rowsNum);
      } else {
        this.rowData = this.dataBackground.slice(
          this.dataBackground.length - this.rowsNum,
          this.dataBackground.length
        );
      }
      this.schemaSelected = "Sort";
      this.updateHeaderDistribution();
      this.updateSampledIndices();
      this.updateHistory();
    },
    onSelectionChanged() {
      const selectedRows = this.gridApi.getSelectedRows()[0];
      if (selectedRows === null || selectedRows === undefined) return;
      delete selectedRows.TOOLBAR_COLUMN; // clean the empty column for toolbar before emitting
      this.$emit("preview-row", selectedRows); // Emit event when the rows are selected
    },

    onRowDoubleClicked(e) {
      if (e.data === null || e.data === undefined) return;
      let startIndex = this.data.indexOf(e.data);
      let endIndex = startIndex + this.stepSize;
      // new domain = [startIndex, endIndex)
      this.updateSampledData([startIndex, endIndex]);
      // this.$refs.form.onUpdateSchema("Stride"); // After focusing on one row, the sampling schema is reverted back to stride
    },

    drawHeaderDistribution() {
      if (this.rowHeight <= 25) return; // Don't draw distribution when there is too many colors on screen

      // Draw the distribution for each column with every node on screen
      for (let i = 0; i < this.completedDescriptors.length; i++) {
        try {
          let itemColorsAndIndice = {};
          const colorArr = [];
          const indexArr = [];
          const data = this.rowData.map(
            e => e[this.completedDescriptors[i].name]
          );
          for (let j = 0; j < data.length; j++) {
            const index = this.completedDescriptors[i].getIndex(data[j]);

            if (index === -1) continue; // Users do not provide a getIndex function
            indexArr.push(index);
            colorArr.push(this.color(j));
          }
          itemColorsAndIndice.color = colorArr;
          itemColorsAndIndice.index = indexArr;
          createHeaderDistribution(
            this.completedDescriptors[i],
            itemColorsAndIndice,
            i
          );
        } catch (error) {
          console.error(
            `Cannot create histogram for descriptor ${this.completedDescriptors[i].name}, please check if the format of the descriptor is correct\n`,
            error
          );
        }
      }
    },

    updateSampledData(newRange) {
      let [startIndex, endIndex] = newRange;
      let newRangeData = this.data.filter((e, i) => {
        return i >= startIndex && i < endIndex;
      });
      let stepSize = Math.ceil(newRangeData.length / this.rowsNum);
      let newRangeSampledData = this.sampleFunction(stepSize)(newRangeData);
      this.rowData = newRangeSampledData;
      this.updateHistory();
      this.updateSampledIndices();
      this.updateHeaderDistribution();
    },

    postSort() {
      if (this.rowsNum === 0) return; // the grid has not inited properly
      const column = this.gridOptions.api.getSortModel()[0];
      if (!column) return;

      const dataset = this.data.slice(0);
      this.sortOneColumn(dataset, column.colId);
      this.dataBackground = dataset;
    },
    sortOneColumn(dataset, column) {
      let index = this.completedDescriptors.map(e => e.name).indexOf(column);
      const ordinalTypes = [
        "String",
        "Categorical",
        "Longtext",
        "List",
        "Link"
      ];

      dataset.sort((a, b) => {
        let val1 = a[column];
        let val2 = b[column];
        if (
          this.completedDescriptors[index] &&
          this.completedDescriptors[index].comparator
        ) {
          // The user provides a comparator
          return this.completedDescriptors[index].comparator(val1, val2);
        } else {
          if (ordinalTypes.includes(this.completedDescriptors[index].type)) {
            return StringComparator(val1, val2);
          } else {
            return NumberComparator(val1, val2);
          }
        }
      });
    },
    updateD3Color() {
      this.color = d3
        .scaleSequential()
        .domain([0, Math.ceil(this.rowsNum)])
        .interpolator(d3.interpolateViridis);
    },
    updateHeaderDistribution() {
      this.gridOptions.api.refreshHeader(); // this clears the previous drawn distribution
      this.drawHeaderDistribution(); // redraw the distribution
    },

    updateSampledIndices() {
      this.sampledIndices = [];
      this.sampledIndicesColors = [];

      this.rowData.forEach((row, i) => {
        let index = this.dataBackground.indexOf(row);
        let color = this.rowHeight > 25 ? this.color(i) : "#d0ccd0";
        this.sampledIndicesColors.push(color);
        this.sampledIndices.push(index);
      });
    },
    updateHistory() {
      this.history = this.history.slice(0, this.currentHistoryIndex + 1); // only keeps the history up to the current index
      this.history.push({
        data: this.rowData,
        height: this.rowHeight,
        schema: this.schemaSelected
      }); // stores the new data into the history
      this.currentHistoryIndex++; // make the current history up one level
    },
    previousHistory() {
      if (this.currentHistoryIndex <= 0) return;
      let nextLevel = this.history[--this.currentHistoryIndex];
      if (this.rowHeight !== nextLevel.height) {
        this.changeRowHeight(nextLevel.height);
        this.updateD3Color();
      }
      this.rowData = nextLevel.data;
      this.schemaSelected = nextLevel.schema;
      this.updateSampledIndices();
      this.updateHeaderDistribution();
    },
    nextHistory() {
      if (this.currentHistoryIndex >= this.history.length - 1) return;
      let nextLevel = this.history[++this.currentHistoryIndex];
      if (this.rowHeight !== nextLevel.height) {
        this.changeRowHeight(nextLevel.height);
        this.updateD3Color();
      }
      this.rowData = nextLevel.data;
      this.schemaSelected = nextLevel.schema;
      this.updateSampledIndices();
      this.updateHeaderDistribution();
    },
    updateRowDataAccordingToHeight() {
      // Todo: here should use the sampling function
      this.rowData = this.data.filter((x, i) => i % this.stepSize === 0);
      // this.rowData = this.sampleFunction(this.stepSize)(this.data);
      this.updateHistory();
      this.updateSampledIndices();
      this.updateD3Color();
      this.updateHeaderDistribution();
    },
    changeRowHeight(newHeight) {
      if (newHeight > 25) {
        this.rowsNum = Math.floor(this.gridHeight / (newHeight + 1)); // extra 1px comes from the ag-grid default css margin
      } else {
        this.rowsNum = Math.floor(this.gridHeight / newHeight); // customized css does not have 1px margin
      }
      this.rowHeight = newHeight;
      this.gridOptions.rowHeight = newHeight;
      this.gridOptions.api.resetRowHeights();
      this.gridOptions.api.paginationSetPageSize(5000); // this forces the grid to fill in as many rows as possible
      this.gridOptions.api.redrawRows(); // this forces rerendering the whole grid
    },
    changeRowHeightAndupdateRowData(newHeight) {
      this.changeRowHeight(newHeight);
      this.updateRowDataAccordingToHeight();
    },
    onSampleFunctionChanged(schema) {
      // let stepSize = this.data.length / this.rowsNum;

      const schemaDontNeedTypes = ["Sort", "Random", "Stride"];
      if (
        this.missingDescriptor.includes(this.columnSelected) &&
        !schemaDontNeedTypes.includes(schema)
      ) {
        return; // Cannot select sample methods that use the information of type to infer
      }

      this.schemaSelected = schema;

      switch (schema) {
        case "Sort":
          this.sampleFunction = stepSize => {
            return data => {
              let rowsNum = data.length / stepSize;
              return data.filter((x, i) => i < rowsNum);
            };
          };
          break;

        case "Stride":
          this.sampleFunction = stepSize => {
            return data => {
              return data.filter((x, i) => i % stepSize === 0);
            };
          };
          break;
        case "Max":
          this.sampleFunction = stepSize => {
            return data => {
              let partition = [];
              for (let j = 0; j < this.rowsNum; j++) {
                let bucket = [];
                for (
                  let i = 0;
                  i < stepSize && stepSize * j + i < data.length;
                  i++
                ) {
                  bucket.push(data[j * stepSize + i]);
                }
                let dataset = bucket.slice(0);
                this.sortOneColumn(dataset, this.columnSelected);

                const smallestIndex = bucket.indexOf(
                  dataset[dataset.length - 1]
                );
                partition.push(data[stepSize * j + smallestIndex]);
              }
              return partition;
            };
          };
          break;
        case "Random":
          this.sampleFunction = stepSize => {
            return data => {
              let partition = [];
              for (let j = 0; j < this.rowsNum; j++) {
                partition.push(
                  data[j * stepSize + Math.floor(Math.random() * stepSize)]
                );
              }
              return partition;
            };
          };
          break;
        case "Min":
          this.sampleFunction = stepSize => {
            return data => {
              let partition = [];
              for (let j = 0; j < this.rowsNum; j++) {
                let bucket = [];
                for (
                  let i = 0;
                  i < stepSize && stepSize * j + i < data.length;
                  i++
                ) {
                  bucket.push(data[j * stepSize + i]);
                }
                let dataset = bucket.slice(0);
                this.sortOneColumn(dataset, this.columnSelected);

                const smallestIndex = bucket.indexOf(dataset[0]);
                partition.push(data[stepSize * j + smallestIndex]);
              }
              return partition;
            };
          };
          break;
        case "Mode":
          this.sampleFunction = stepSize => {
            return data => {
              let partition = [];

              for (let j = 0; j < this.rowsNum; j++) {
                let bucket = [];
                let counts = [[], []]; // index 0 is the key, index 1 is the value

                for (
                  let i = 0;
                  i < stepSize && stepSize * j + i < data.length;
                  i++
                ) {
                  let cur = data[j * stepSize + i][this.columnSelected];
                  bucket.push(cur);
                  if (counts[0].includes(cur)) {
                    let index = counts[0].indexOf(cur);
                    counts[1][index] += 1;
                  } else {
                    counts[0].push(cur);
                    let index = counts[0].indexOf(cur);
                    counts[1][index] = 1;
                  }
                }

                let maxValue = Math.max(...counts[1]);
                let maxKeys = counts[0].filter(
                  (k, i) => counts[1][i] === maxValue
                );
                // keys with the most counts

                let oneMaxKey =
                  maxKeys[Math.floor(Math.random() * maxKeys.length)];
                // randomly selects one key with the most counts

                let mostOccuringIndex = bucket.lastIndexOf(oneMaxKey);
                partition.push(data[stepSize * j + mostOccuringIndex]);
              }
              return partition;
            };
          };
          break;
      }
      this.rowData = this.sampleFunction(this.stepSize)(this.dataBackground);
      this.updateHeaderDistribution();
      this.updateHistory();
      this.updateSampledIndices();
    }
    // sampleOnRange(index) {
    //   const sampledData = this.data.filter(
    //     (x, i) => i >= index && i < index + this.stepSize
    //   );
    //   this.gridOptions.api.setRowData(sampledData);
    //   this.firstItemIndex = index;
    // }
    // scrollChangeRowHeight(e) {
    //   // let isAnimationPending = this.gridApi.isAnimationFrameQueueEmpty();
    //   // if (isAnimationPending) return; // If previous scrolling is still pending

    //   let nextIndex;
    //   if (e.deltaY > 0) {
    //     // scroll up
    //     const index = this.rowHeights.indexOf(this.rowHeight);
    //     if (index === 0) return; // cannot decrease the row height if it is minimal
    //     nextIndex = index - 1;
