<template>
  <v-layout column class="wrapper">
    <v-tooltip left>
      <template v-slot:activator="{ on }">
        <select @change="onRowHeightChanged($event)" v-on="on">
          <option disabled selected style="display:none;">{{rowHeight}}</option>
          <option v-for="height in rowHeights" v-bind:value="height" v-bind:key="height">{{height}}</option>
        </select>
      </template>
      <span>Change row height</span>
    </v-tooltip>
    <v-tooltip left>
      <template v-slot:activator="{ on }">
        <div v-on="on">
          <i @click="previousHistory" class="fas fa-w fa-2x fa-caret-left"></i>
          <i @click="nextHistory" class="fas fa-w fa-2x fa-caret-right"></i>
        </div>
      </template>
      <span>Revert to previous/next sampling history</span>
    </v-tooltip>
    <v-tooltip right>
      <template v-slot:activator="{ on }">
        <div v-on="on">
          <v-dialog v-model="dialog" scrollable width="500">
            <template v-slot:activator="{ on }">
              <i class="fas fa-columns" v-on="on"></i>
            </template>

            <v-card>
              <v-card-text style="height: 80vh;">
                <v-divider></v-divider>

                <draggable :list="columns" @end="onDrop">
                  <transition-group>
                    <v-list-tile v-for="column in columns" :key="column.name">
                      <v-card-text>{{column.name}}</v-card-text>
                      <v-spacer></v-spacer>
                      <v-card-actions>
                        <v-checkbox v-model="column.hide" :disabled="column.pin" label="Hide"></v-checkbox>
                        <!-- <v-checkbox v-model="column.pin" :disabled="column.hide" label="Pin"></v-checkbox> -->
                      </v-card-actions>
                    </v-list-tile>
                  </transition-group>
                </draggable>
              </v-card-text>

              <v-divider></v-divider>

              <v-card-actions>
                <v-btn color="blue darken-1" flat @click="dialog = false">Close</v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </div>
      </template>
      <span>Reorder Column</span>
    </v-tooltip>
  </v-layout>
</template>

<script>
import Vue from "vue";
import draggable from "vuedraggable";

export default Vue.extend({
  components: {
    draggable
  },
  computed: {
    parentComponent() {
      return this.$parent.$parent;
    },
    rowHeight() {
      return this.parentComponent.rowHeight;
    }
  },

  data() {
    return {
      rowHeights: [5, 10, 25, 30, 50],
      dialog: false,
      columns: []
    };
  },

  watch: {
    columns: {
      handler() {
        this.columns.forEach(column => {
          this.parentComponent.columnApi.setColumnVisible(
            column.name,
            !column.hide // visible is the opposite of hide
          );
        });
      },
      deep: true,
      immediate: false
    },
    dialog(opened) {
      if (!opened) {
        this.parentComponent.updateHeaderDistribution();
      }
    }
  },
  mounted() {
    const columns = this.parentComponent.columnApi
      .getAllGridColumns() // getAllGridColumns maintains the order of the columns in the grid
      .map(column => {
        return {
          name: column.colId,
          hide: !column.visible,
          pin: false
        };
      });
    columns.shift(); // Removes the toolbar index

    this.columns = columns;
  },
  methods: {
    onRowHeightChanged(event) {
      let newHeight = Number(event.target.value);
      this.parentComponent.changeRowHeightAndupdateRowData(newHeight);
    },
    previousHistory() {
      this.parentComponent.previousHistory();
    },
    nextHistory() {
      this.parentComponent.nextHistory();
    },
    onDrop(event) {
      const { oldIndex, newIndex } = event;
      // index 0 is the toolbar column, so we need to always add one to avoid moving the toolbar column
      this.parentComponent.columnApi.moveColumnByIndex(
        oldIndex + 1,
        newIndex + 1
      );
    }
  }
});
</script>

<style scoped>
select {
  transform: translate(25%, 0);
}

i {
  margin: 1px;
}
.wrapper {
  position: absolute;
  left: 15%;
}
</style>