<template>
  <scrolly :passive-scroll="false" @scrollchange="logScrollLayout">
    <!-- The area that is shown  -->
    <hr
      v-for="n in height"
      :key="n"
      v-bind:style="{ 'background-color' : backgroundColor(n) }"
      v-bind:class="isBoundary(n)"
    />
    <!-- The area that is hidden from the user, but necessary to calculate the scrollbar height -->
    <scrolly-viewport>
      <!-- scrollbar-height = (this.height / (coefficient * dataLen)) * this.height -->
      <hr v-for="n in Math.round(dataLen * 3)" :key="n" class="hide" />
    </scrolly-viewport>
    <!-- the default min-height is 20% -->
    <scrolly-bar
      :visibility="'visible'"
      :style="{'min-height': 0, width: '100%'}"
      axis="y"
      v-on:dblclick.native="changeRange()"
    ></scrolly-bar>
  </scrolly>
</template>

<script>
import { Scrolly, ScrollyViewport, ScrollyBar } from "vue-scrolly";

export default {
  /*   
  OverlayScrollbar component expects 3 props:
    rows: number of the rows of the data table
    highlightedIndices: an array of the indices shown on each page 

    Working features:
      1. It can calculate the view height of the browser 
      2. It can highlight the selected column 
      3. Can sample the rows based on the view height
    Pending features:
      1. Range selection for the ag-grid using mouse event
      2. The calculated view height of the browser is not dynamic 
      3. Reflect more info of the rows selected by listening to the event of ag-grid
      4. Should add css style for bucket boundary
  */
  name: "OverlayScrollbar",
  props: [
    "dataLen",
    "highlightedIndices",
    "highlightedIndicesColors",
    "bucketIndices",
    "stepSize"
  ],
  beforeCreate() {
    this.height = Math.max(
      document.documentElement.clientHeight,
      window.innerHeight
    ); // caculating the vh
  },
  data() {
    return {
      stepSizePX: 0, // bucket size at px level
      highlightedPX: [], // highlighted indices at px level
      bucketsBoundaryPX: [],
      // selectedPX: [], // selectedPX indices at px level
      barTop: 0,
      barHeight: 0,
      barHeightCoefficient: 1 // used to init the default bar height,
    };
  },
  components: {
    Scrolly,
    ScrollyViewport,
    ScrollyBar
  },
  methods: {
    changeRange() {
      this.$parent.updateSampledData(this.sampledRange);
    },

    logScrollLayout(scrollLayout) {
      // this event always comes earlier than 'changeRange'
      let param = scrollLayout.y;
      this.barHeight = param.barHeight;
      this.barTop = param.barTop;
    },
    backgroundColor(n) {
      let index = this.highlightedPX.indexOf(n);
      if (index !== -1) return this.highlightedIndicesColors[index];
    },
    isBoundary(n) {
      let boundary = this.bucketsBoundaryPX.indexOf(n);
      if (boundary !== -1) return "boundary";
    }
  },
  computed: {
    sampledRange() {
      // range = [barTop, barTop + barHeight] in px level
      return [
        Math.round(this.barTop * this.stepSizePX),
        Math.round((this.barTop + this.barHeight) * this.stepSizePX)
      ];
    }
  },
  watch: {
    stepSize() {
      // scrollbar-height = (this.height / (coefficient * this.dataLen)) * this.height
      // scrollbar-height = this.height * (this.stepSize / this.dataLen) // want the default scrollbar-height = the height of a bucket
      // this.stepSize / this.dataLen = this.height / (coefficient * this.dataLen)
      // this.stepSize / (this.dataLen * this.height) = 1 / (coefficient * this.dataLen)
      // coefficient = (1 / (this.stepSize / (this.dataLen * this.height))) / this.dataLen

      this.barHeightCoefficient =
        1 / (this.stepSize / (this.dataLen * this.height)) / this.dataLen;
    },
    dataLen() {
      this.stepSizePX = this.dataLen / this.height;
    },
    highlightedIndices() {
      this.highlightedPX = this.highlightedIndices.map(n => {
        return Math.round(n / this.stepSizePX);
      });
    },
    bucketIndices() {
      this.bucketsBoundaryPX = this.bucketIndices.map(n => {
        return Math.round(n / this.stepSizePX);
      });
    }
  }
};
</script>

<style scoped>
.scrolly-viewport {
  z-index: -1; /* Avoid collision with other elements  */
}
.hide {
  visibility: hidden; /* Hide the original data to make the scrollbar not scroll the page*/
}
hr {
  border: none;
  height: 1px;
  background-color: grey;
}
.boundary {
  /* background-color: #d0ccd0; */
}

.scrolly-bar {
  margin: 0;
  padding: 0;
  opacity: 1;
  border: 0;
}
</style>
