import * as d3 from "d3";
import {
	StringComparator,
	NumberComparator
} from "./components/CardComparator/comparator/custom-comparator";

function getNumericalHistogram(values) {
	let max = d3.max(values);
	let min = d3.min(values);
	let bins = 10;
	let x = d3.scaleLinear().domain([min, max]);

	let data = d3.histogram().thresholds(x.ticks(bins))(values);
	data = data.map(e => e.length);

	return data;
}

function getNumericalBucketName(values) {
	let max = d3.max(values);
	let min = d3.min(values);

	let bins = 10;
	let x = d3.scaleLinear().domain([min, max]);
	let data = d3.histogram().thresholds(x.ticks(bins))(values);

	let names = data.map(e => e.x0);
	names.push(data[data.length - 1].x1); // push the last index to the array

	return names;
}

// Turn an array of obj to an 2 dimensional array with key value pair
function flattenData(file) {
	const flattens = [];
	for (const key in file[0]) {
		const flatten = file.reduce((acc, cur) => {
			return acc.concat(cur[key]);
		}, []);
		flattens.push({ key, data: flatten });
	}
	return flattens;
}

function countData(flattens) {
	return flattens.map(e => {
		const count = e.data
			.filter(e => e != null)
			.reduce((all, cur) => {
				if (cur in all) {
					all[cur]++;
				} else {
					all[cur] = 1;
				}
				return all;
			}, {});

		const data = [];
		const value = Object.values(count);
		data.push(value);

		return { key: e.key, data };
	});
}

function getOrdinalBucketName(flattens) {
	return flattens.map(e => {
		return {
			key: e.key,
			data: [...new Set(e.data)].filter(x => x != null)
		};
	});
}


export default function createFileDescriptor(file, fileDescriptor) {
	const flattens = flattenData(file);
	const counts = countData(flattens);
	const ordinalBucketNames = getOrdinalBucketName(flattens);

	return fileDescriptor.map(e => {
		const data = flattens.filter(element => element.key === e.name)[0]
			.data;
		const ordinalBucketCount = counts.filter(
			element => element.key === e.name
		)[0].data[0];
		const ordinalBucketName = ordinalBucketNames.filter(
			element => element.key === e.name
		)[0].data;

		const numericalBucketName = getNumericalBucketName(data);
		const numericalHistogram = getNumericalHistogram(data);

		const ordinalTypes = ["String", "Categorical", "Longtext", "List", "Link"];
		return {
			name: e.name,
			type: e.type,
			bucketName:
				ordinalTypes.includes(e.type) ? ordinalBucketName : numericalBucketName,
			// Here we pass the string length as x-axis
			histogram:
				ordinalTypes.includes(e.type) ? ordinalBucketCount : numericalHistogram,
			comparator:
				ordinalTypes.includes(e.type) ? StringComparator : NumberComparator,
			// return the index (which bucket) of "value" in the histogram
			getIndex: function (value) {
				if (ordinalTypes.includes(e.type)) {
					return ordinalBucketName.indexOf(value);
				} else {
					let index = -1;
					for (let i = 1; i < numericalBucketName.length; i++) {
						if (numericalBucketName[i] > value) {
							index = i - 1;
							break;
						}
					}
					// const lastIndex = numericalBucketName.length - 1;
					// if (numericalBucketName[lastIndex] === value) index = lastIndex; // The last bucket is a closed interval

					return index;
				}
			}
		};
	});
}