<template>
  <div style="overflow: auto; vertical-align: top;"></div>
</template>
<script>
import * as d3 from "d3";

export default {
  name: "Distribution",
  props: ["values", "descriptor"],
  data() {
    return {};
  },
  mounted() {
    this.createDistribution();
  },
  methods: {
    createDistribution() {
      if (
        !this.descriptor ||
        !this.descriptor.histogram ||
        !this.descriptor.bucketName
      ) {
        return;
      }

      if (
        this.descriptor.histogram.length === this.descriptor.bucketName.length
      ) {
        this.createOrdinalChart();
      } else {
        this.createNumericalChart();
      }
    },
    createOrdinalChart() {
      if (this.descriptor.bucketName.length > 50) {
        console.warn(
          `${this.descriptor.name} has ${this.descriptor.bucketName.length} buckets, maximum threshold to fit on the screen is 50`
        );
        return;
      }

      const domain = [];
      const data = [];
      for (let i = 0; i < this.descriptor.histogram.length; i++) {
        const name = this.descriptor.bucketName[i];
        const value = this.descriptor.histogram[i];
        const obj = {};
        obj["value"] = value;
        obj["name"] = name;
        data.push(obj);
        domain.push(name);
      }

      const color = "grey";
      const margin = { top: 20, right: 0, bottom: 20, left: 30 },
        // Each bucket is at least 50px wide
        width = Math.max(400, domain.length * 50) - margin.left - margin.right,
        height = 120 - margin.top - margin.bottom;
      const x = d3
        .scaleBand()
        .rangeRound([0, width])
        .domain(domain)
        .padding(0.1);

      const binIndexArr = [];
      if (this.descriptor.getIndex) {
        // Users provide the indicator function
        for (let i = 0; i < this.values.length; i++) {
          let index = this.descriptor.getIndex(this.values[i]);
          if (index === -1) continue;
          binIndexArr.push(index);
        }
      } else {
        // Default to find the binIndex
        for (let i = 0; i < this.values.length; i++) {
          const value = this.values[i];
          let index = -1;

          data.forEach((e, j) => {
            if (e.name === value) index = j;
          });
          if (index === -1) continue;
          binIndexArr.push(index);
        }
      }

      const yMin = Math.min(...data.map(e => e.value));
      const yMax = Math.max(...data.map(e => e.value));

      const colorScale = d3
        .scaleLinear()
        .domain([yMin, yMax])
        .range([d3.rgb(color).brighter(), d3.rgb(color).darker()]);
      const y = d3
        .scaleLinear()
        .domain([0, yMax])
        .range([height, 0]);
      const xAxis = d3.axisBottom(x).tickFormat(function(d) {
        const str = d.toString();
        if (str.length > 5) return str.substring(0, 4) + "..";
        return d;
      });

      const svg = d3
        .select(this.$el)
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      const tooltip = d3
        .select("#item-comparator")
        .append("div")
        .attr("class", "tooltip")
        .style("display", "inline-block")
        .style("position", "absolute")
        .style("background", "#f4f4f4")
        .style("padding", "5 15px")
        .style("border", "1px #333 solid")
        .style("border-radius", "5px")
        .style("opacity", 0);

      const bar = svg
        .selectAll(".bar")
        .data(data)
        .enter()
        .append("g")
        .attr("class", "bar")
        .attr("transform", function(d) {
          return "translate(" + x(d.name) + "," + 0 + ")";
        });

      svg
        .append("defs")
        .append("marker")
        .attr("id", "arrow")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 0)
        .attr("markerWidth", 6)
        .attr("markerHeight", 6)
        .attr("orient", "auto")
        .append("path")
        .attr("class", "marker")
        .attr("d", "M0,-5L10,0L0,5");

      const binIndexMap = new Array(data.length).fill(0);
      binIndexArr.forEach(e => (binIndexMap[e] += 1));

      const div = svg.selectAll("line.arrow");
      const arrowDiv = svg.selectAll("arrow");
      binIndexMap.forEach((e, index) => {
        for (let j = 0; j < e; j++) {
          div
            .data(
              data.filter(function(d, i) {
                return i === index;
              })
            )
            .enter()
            .append("line")
            .attr("class", "arrow")
            .attr("x1", function(d) {
              return x(d.name) + 5;
            })
            .attr("x2", function(d) {
              return x(d.name) + 5;
            })
            .attr("y1", function(d) {
              return y(d.value) - 40 - j * 8;
            })
            .attr("y2", function(d) {
              return y(d.value) - 10 - j * 8;
            })
            .attr("marker-end", "url(#arrow)");
        }
      });

      for (let j = binIndexArr.length - 1; j >= 0; j--) {
        const binIndex = binIndexArr[j];
        const offset = --binIndexMap[binIndex];
        arrowDiv
          .data(
            data.filter(function(d, i) {
              return i === binIndex;
            })
          )
          .enter()
          .append("text")
          .attr("class", "label")
          .style("font-size", "10px")
          .attr("x", function(d) {
            return x(d.name) + 15;
          })
          .attr("y", function(d) {
            return y(d.value) - 5 - offset * 8;
          })
          .attr("class", "barsEndlineText")
          .attr("text-anchor", "middle")
          .text(j + 1);
      }

      bar
        .append("rect")
        .attr("x", 1)
        .attr("width", function() {
          return x.bandwidth();
        })
        .attr("height", 0)
        .attr("y", height)
        .attr("fill", function(d) {
          return colorScale(d.value);
        })
        .on("mouseover", function(d) {
          tooltip.transition().style("opacity", 1);
          tooltip
            .html(`${d.name}: ${d.value}`)
            .style("left", d3.event.pageX - 20 + "px")
            .style("top", d3.event.pageY - 30 + "px");
          d3.select(this).style("opacity", 0.5);
        })
        .on("mouseout", function() {
          tooltip.transition().style("opacity", 0);
          d3.select(this).style("opacity", 1);
        });

      // Add circles to the rectangles with small heights to help show tooltip
      bar
        .append("circle")
        .filter(function(d) {
          return (height - y(d.value)) / height < 0.1;
        })
        .attr("cy", function(d) {
          return y(d.value) - 8;
        })
        .attr("cx", x.bandwidth() / 2)
        .attr("r", 10)
        .attr("fill", function(d) {
          return colorScale(d.value);
        })
        .on("mouseover", function(d) {
          tooltip.transition().style("opacity", 1);
          tooltip
            .html(`${d.name}: ${d.value}`)
            .style("left", d3.event.pageX - 20 + "px")
            .style("top", d3.event.pageY - 30 + "px");
          d3.select(this).style("opacity", 0.5);
        })
        .on("mouseout", function() {
          tooltip.transition().style("opacity", 0);
          d3.select(this).style("opacity", 1);
        });

      svg
        .selectAll("rect")
        .transition()
        .attr("y", function(d) {
          return y(d.value);
        })
        .attr("height", function(d) {
          return height - y(d.value);
        })
        .duration(800)
        .delay(function(d, i) {
          return i * 50;
        });

      svg
        .append("g")
        .attr("class", "axis")
        .call(
          d3
            .axisLeft(y)
            .ticks(3)
            .tickFormat(d3.format(".0s"))
        );

      svg
        .append("g")
        .attr("class", "x axis")
        .attr("transform", "translate(0," + height + ")")
        .call(xAxis);
    },

    createNumericalChart() {
      if (this.descriptor.bucketName.length > 200) {
        console.warn(
          `${this.descriptor.bucket} has ${this.descriptor.bucketName.length} buckets, maximum threshold to fit on the screen is 200`
        );
        return;
      }

      const buckets = [];

      for (let i = 1; i < this.descriptor.bucketName.length; i++) {
        const obj = {};
        obj.offset = this.descriptor.bucketName[i - 1];
        obj.y = this.descriptor.histogram[i - 1];
        obj.width = this.descriptor.bucketName[i] - obj.offset;

        obj.height = obj.y / obj.width;

        if (obj.width !== 0) buckets.push(obj);
        // If the width is 0 by any chance, the height would be infinity.
      }

      const color = "grey";

      const margin = { top: 20, right: 0, bottom: 20, left: 30 },
        // Each bucket is at least 50px wide
        width = Math.max(400, buckets.length * 50) - margin.left - margin.right,
        height = 120 - margin.top - margin.bottom;

      const binIndexArr = [];
      if (this.descriptor.getIndex) {
        // Users provide the indicator function
        for (let i = 0; i < this.values.length; i++) {
          let index = this.descriptor.getIndex(this.values[i]);
          if (index === -1) continue;
          binIndexArr.push(index);
        }
      } else {
        for (let j = 0; j < this.values.length; j++) {
          const value = this.values[j];
          let index = -1;

          for (let i = 0; i < buckets.length; i++) {
            if (buckets[i].offset > value) {
              index = i;
              break;
            }
          }
          if (index === -1) continue;
          binIndexArr.push(index);
        }
      }
      const max =
        buckets[buckets.length - 1].offset + buckets[buckets.length - 1].width;
      const min = buckets[0].offset;

      const x = d3.scaleLinear().range([0, width]);
      const y = d3.scaleLinear().range([height, 0]);

      const yMax = d3.max(buckets, function(d) {
        return d.y;
      });
      const yMin = d3.min(buckets, function(d) {
        return d.y;
      });
      x.domain([min, max]);
      y.domain([
        0,
        d3.max(
          buckets.map(function(d) {
            return d.height;
          })
        )
      ]);

      const colorScale = d3
        .scaleLinear()
        .domain([yMin, yMax])
        .range([d3.rgb(color).brighter(), d3.rgb(color).darker()]);

      const xAxis = d3.axisBottom(x).tickFormat(function(d) {
        const str = d.toString();
        if (str.length > 5) return str.substring(0, 4) + "..";
        return d;
      });

      const tooltip = d3
        .select("#item-comparator")
        .append("div")
        .attr("class", "tooltip")
        .style("display", "inline-block")
        .style("position", "absolute")
        .style("background", "#f4f4f4")
        .style("padding", "5 15px")
        .style("border", "1px #333 solid")
        .style("border-radius", "5px")
        .style("opacity", 0);

      const svg = d3
        .select(this.$el)
        .append("svg")
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        .append("g")
        .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

      svg
        .append("defs")
        .append("marker")
        .attr("id", "arrow")
        .attr("viewBox", "0 -5 10 10")
        .attr("refX", 0)
        .attr("markerWidth", 6)
        .attr("markerHeight", 6)
        .attr("orient", "auto")
        .append("path")
        .attr("class", "marker")
        .attr("d", "M0,-5L10,0L0,5");

      const binIndexMap = new Array(buckets.length).fill(0);
      binIndexArr.forEach(e => (binIndexMap[e] += 1));

      const div = svg.selectAll("line.arrow");
      const arrowDiv = svg.selectAll("arrow");

      binIndexMap.forEach((e, index) => {
        for (let j = 0; j < e; j++) {
          div
            .data(
              buckets.filter(function(d, i) {
                return i === index;
              })
            )
            .enter()
            .append("line")
            .attr("class", "arrow")
            .attr("x1", function(d) {
              return x(d.offset) + 5;
            })
            .attr("x2", function(d) {
              return x(d.offset) + 5;
            })
            .attr("y1", function(d) {
              return y(d.height) - 40 - j * 8;
            })
            .attr("y2", function(d) {
              return y(d.height) - 5 - j * 8;
            })

            .attr("marker-end", "url(#arrow)");
        }
      });

      for (let j = binIndexArr.length - 1; j >= 0; j--) {
        const binIndex = binIndexArr[j];
        const offset = --binIndexMap[binIndex];
        arrowDiv
          .data(
            buckets.filter(function(d, i) {
              return i === binIndex;
            })
          )
          .enter()
          .append("text")
          .attr("class", "label")
          .style("font-size", "10px")
          .attr("x", function(d) {
            return x(d.offset) + 15;
          })
          .attr("y", function(d) {
            return y(d.height) - offset * 8;
          })
          .attr("class", "barsEndlineText")
          .attr("text-anchor", "middle")
          .text(j + 1);
      }

      const bar = svg
        .selectAll(".bar")
        .data(buckets)
        .enter()
        .append("g")
        .attr("class", "bar")
        .attr("transform", function(d) {
          return "translate(" + x(d.offset) + "," + 0 + ")";
        });

      bar
        .append("rect")
        .attr("x", 1)
        .attr("width", function(d) {
          return x(d.width + d.offset) - x(d.offset) - 1;
        })
        .attr("y", height)
        .attr("height", 0)
        .attr("fill", function(d) {
          return colorScale(d.y);
        })
        .on("mouseover", function(d) {
          tooltip.transition().style("opacity", 1);
          tooltip
            .html(`[${d.offset}, ${d.offset + d.width}): ${d.y}`)
            .style("left", d3.event.pageX - 20 + "px")
            .style("top", d3.event.pageY - 30 + "px");
          d3.select(this).style("opacity", 0.5);
        })
        .on("mouseout", function() {
          tooltip.transition().style("opacity", 0);
          d3.select(this).style("opacity", 1);
        });

      // Add circles to the rectangles with small heights to help show tooltip
      bar
        .append("circle")
        .filter(function(d) {
          return (height - y(d.height)) / height < 0.1;
        })
        .attr("cy", function(d) {
          return y(d.height) - 8;
        })
        .attr("cx", function(d) {
          return (x(d.width + d.offset) - x(d.offset)) / 2;
        })
        .attr("r", 10)
        .attr("fill", function(d) {
          return colorScale(d.height);
        })
        .on("mouseover", function(d) {
          tooltip.transition().style("opacity", 1);
          tooltip
            .html(`[${d.offset}, ${d.offset + d.width}): ${d.y}`)
            .style("left", d3.event.pageX - 20 + "px")
            .style("top", d3.event.pageY - 30 + "px");
          d3.select(this).style("opacity", 0.5);
        })
        .on("mouseout", function() {
          tooltip.transition().style("opacity", 0);
          d3.select(this).style("opacity", 1);
        });

      svg
        .selectAll("rect")
        .transition()
        .attr("y", function(d) {
          return y(d.height);
        })
        .attr("height", function(d) {
          return height - y(d.height);
        })
        .duration(800)
        .delay(function(d, i) {
          return i * 50;
        });

      svg
        .append("g")
        .attr("class", "axis")
        .attr("transform", "translate(0," + height + ")")
        .call(xAxis);

      svg
        .append("g")
        .attr("class", "axis")
        .call(
          d3
            .axisLeft(y)
            .ticks(3)
            .tickFormat(d3.format(".0s"))
        );
    }
  },
  watch: {
    values() {
      d3.select(this.$el).html(null); // new value passed in, clear the graph
      this.createDistribution(); // redraw the graph
    }
  }
};
</script>

<style scoped>
.axis {
  font: 12px sans-serif;
}
</style>