<template>
  <div>
    <!-- header distribution -->
    <div :id="id"></div>

    <!-- other info in header -->
    <div @click="onSortRequested($event)">
      <strong class="customHeaderLabel">{{params.displayName}}</strong>
    </div>

    <div class="outer">
      <div ref="menuButton" @click="onMenuClicked($event)">
        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <i class="fas fa-search" v-on="on"></i>
          </template>
          <span>Search</span>
        </v-tooltip>
      </div>

      <div>
        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <i class="fas fa-thumbtack" @click="onPinnedColumn" v-on="on"></i>
          </template>
          <span>Pin this column</span>
        </v-tooltip>
      </div>

      <div v-if="params.column.sort">
        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <div v-on="on">
              <i class="fas fa-fw fa-filter"></i>
              <select v-model="parentSchema" @change="onSchemaChanged($event)">
                <option v-bind:value="parentSchema" selected="selected">{{parentSchema}}</option>
                <option
                  v-for="option in optionsAvailable"
                  v-bind:value="option"
                  v-bind:key="option"
                >{{option}}</option>
              </select>
            </div>
          </template>
          <span>Select sample method</span>
        </v-tooltip>
      </div>

      <div @click="onSortRequested($event)">
        <v-tooltip top>
          <template v-slot:activator="{ on }">
            <div v-on="on">
              <i :class="ascSort" class="fas fa-long-arrow-alt-up"></i>
              <i :class="descSort" class="fas fa-long-arrow-alt-down"></i>
            </div>
          </template>
          <span>Sort Direction (Focus)</span>
        </v-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";

export default Vue.extend({
  computed: {
    id() {
      return "headerDistribution-" + this.params.index;
    },
    parentComponent() {
      return this.$parent.$parent;
    },
    optionsAvailable() {
      return this.schemas.filter(
        e => e !== this.parentComponent.schemaSelected
      );
    },
    parentSchema: {
      get() {
        return this.parentComponent.schemaSelected;
      },
      set() {}
    }
  },
  data() {
    return {
      ascSort: null,
      descSort: null,
      schemas: ["Sort", "Stride", "Max", "Min", "Random", "Mode"]
    };
  },
  beforeMount() {},
  mounted() {
    this.params.column.addEventListener("sortChanged", this.onSortChanged);
    this.onSortChanged();
  },
  methods: {
    onSchemaChanged(event) {
      this.parentComponent.onSampleFunctionChanged(event.target.value);
    },
    onMenuClicked() {
      this.params.showColumnMenu(this.$refs.menuButton);
    },
    onPinnedColumn() {
      this.parentComponent.columnApi.setColumnPinned(
        this.params.displayName,
        "left"
      );
      this.parentComponent.updateHeaderDistribution();
    },

    onSortChanged() {
      this.ascSort = this.descSort = "inactive";
      if (this.params.column.isSortAscending()) {
        this.ascSort = "active";
      } else if (this.params.column.isSortDescending()) {
        this.descSort = "active";
      }
    },

    onSortRequested(event) {
      this.params.progressSort(event.shiftKey);
    }
  }
});
</script>

<style scoped>
.outer {
  /* width: 100%; */
  display: flex;
  justify-content: space-between;
}

.active {
  color: cornflowerblue;
  font-weight: bold;
}
</style>