import * as d3 from "d3";

function createOrdinalChart(descriptor, itemColorsAndIndices, index) {

	if (descriptor.bucketName.length > 50) {
		console.warn(
			`${descriptor.name} has ${descriptor.bucketName.length} buckets, maximum threshold to fit on the screen is 50`
		);
		return;
	}

	const domain = []; // all the header's name
	const data = []; // the data corresponding to each header
	for (let i = 0; i < descriptor.histogram.length; i++) {
		const name = descriptor.bucketName[i];
		const value = descriptor.histogram[i];
		const obj = {};
		obj.value = value;
		obj.name = name;
		data.push(obj);
		domain.push(name);
	}

	const color = "grey";
	const margin = { top: 20, right: 0, bottom: 20, left: 0 },
		width = 300 - margin.left - margin.right,
		height = 80 - margin.top - margin.bottom;
	const x = d3
		.scaleBand()
		.rangeRound([0, width])
		.domain(domain)
		.padding(0.1);

	// finding the max value and min value 
	const yMin = Math.min(...data.map(e => e.value));
	const yMax = Math.max(...data.map(e => e.value));

	const colorScale = d3
		.scaleLinear()
		.domain([yMin, yMax])
		.range([d3.rgb(color).brighter(), d3.rgb(color).darker()]);
	const y = d3
		.scaleLinear()
		.domain([0, yMax])
		.range([height, 0]);

	const svg = d3
		.select(`#headerDistribution-${index}`)
		.append("svg")
		.attr("width", width + margin.left + margin.right)
		.attr("height", height + margin.top + margin.bottom)
		.append("g")
		.attr("transform", "translate(" + margin.left + "," + margin.top + ")");

	const bar = svg
		.selectAll(".bar")
		.data(data)
		.enter()
		.append("g")
		.attr("class", "bar")
		.attr("transform", function (d) {
			return "translate(" + x(d.name) + ", 0)";
		});

	bar
		.append("rect")
		.attr("x", 1)
		.attr("width", function () {
			return x.bandwidth();
		})
		.attr("height", function (d) {
			return height - y(d.value);
		})
		.attr("fill", function (d) {
			return colorScale(d.value);
		});


	// Counting the number of occurence for each index
	// e.g.
	// binIndexArr : [0,1,1,2,3,3,3,3,3]
	// binIndexMap : [1,2,1,5,0,0,0,0,0]
	const binIndexArr = itemColorsAndIndices.index;
	const binIndexMap = new Array(data.length).fill(0);
	const binColorArr = new Array(data.length).fill(new Array());

	binIndexArr.forEach((e, whichRow) => {
		binIndexMap[e] += 1;
		binColorArr[e].push(itemColorsAndIndices.color[whichRow]);
	});

	const arrow_div = svg.selectAll("line.arrow");
	// Drawing the arrows for each bucket

	binIndexMap.forEach((itemIndex, whichRow) => {
		for (let j = 0; j < itemIndex; j++) {
			arrow_div
				.data(
					data.filter(function (d, i) {
						return i === whichRow;
					})
				)
				.enter()
				.append("line")
				.attr("x1", function (d) {
					return x(d.name) + j * 3;
				})
				.attr("y1", height)
				.attr("x2", function (d) {
					return x(d.name) + j * 3;
				})
				.attr("y2", function (d) {
					return y(d.value);
				})
				.style("stroke-width", 3)
				.style("stroke", binColorArr[whichRow][j])
				.style("fill", "none");
		}
	});

	svg
		.selectAll("rect")
		.transition()
		.attr("y", function (d) {
			return y(d.value);
		})
		.attr("height", function (d) {
			return height - y(d.value);
		})
		.duration(1200)
		.delay(function (d, i) {
			return i * 50;
		});

	// Adding x axis

	const xAxis = d3.axisBottom(x).tickFormat(function (d) {
		const str = d.toString();
		if (str.length > 3) return str.substring(0, 2) + '..';
		return d;
	});

	svg
		.append("g")
		.attr("class", "x axis")
		.attr("transform", "translate(0," + height + ")")
		.call(xAxis);
}

function createNumericalChart(descriptor, itemColorsAndIndices, index) {
	if (descriptor.bucketName.length > 200) {
		console.warn(
			`${descriptor.bucket} has ${descriptor.bucketName.length} buckets, maximum threshold to fit on the screen is 200`
		);
		return;
	}

	const buckets = [];

	for (let i = 1; i < descriptor.bucketName.length; i++) {
		const obj = {};
		obj.offset = descriptor.bucketName[i - 1];
		obj.y = descriptor.histogram[i - 1];
		obj.width = descriptor.bucketName[i] - obj.offset;

		obj.height = obj.y / obj.width;

		if (obj.width !== 0) buckets.push(obj);
		// If the width is 0, the height would be infinity.
	}
	const color = "grey";
	const margin = { top: 20, right: 0, bottom: 20, left: 0 },
		width = 300 - margin.left - margin.right,
		height = 80 - margin.top - margin.bottom;

	const max =
		buckets[buckets.length - 1].offset + buckets[buckets.length - 1].width;
	const min = buckets[0].offset;

	const x = d3.scaleLinear().range([0, width]);
	const y = d3.scaleLinear().range([height, 0]);

	const yMax = d3.max(buckets, function (d) {
		return d.y;
	});
	const yMin = d3.min(buckets, function (d) {
		return d.y;
	});
	x.domain([min, max]);
	y.domain([
		0,
		d3.max(
			buckets.map(function (d) {
				return d.height;
			})
		)
	]);

	const xAxis = d3.axisBottom(x).tickFormat(function (d) {
		const str = d.toString();
		if (str.length > 3) return str.substring(0, 2) + '..';
		return d;
	});


	const colorScale = d3
		.scaleLinear()
		.domain([yMin, yMax])
		.range([d3.rgb(color).brighter(), d3.rgb(color).darker()]);


	const svg = d3
		.select(`#headerDistribution-${index}`)
		.append("svg")
		.attr("width", width + margin.left + margin.right)
		.attr("height", height + margin.top + margin.bottom)
		.append("g")
		.attr("transform", "translate(" + margin.left + "," + margin.top + ")");

	const bar = svg
		.selectAll(".bar")
		.data(buckets)
		.enter()
		.append("g")
		.attr("class", "bar")
		.attr("transform", function (d) {
			return "translate(" + x(d.offset) + ", 0)";
		});

	bar
		.append("rect")
		.attr("x", 1)
		.attr("width", function (d) {
			return x(d.width + d.offset) - x(d.offset) - 1;
		})
		.attr("height", function (d) {
			return height - y(d.height);
		})
		.attr("fill", function (d) {
			return colorScale(d.y);
		});


	// Counting the number of occurence for each index
	// e.g.
	// binIndexArr : [0,1,1,2,3,3,3,3,3]
	// binIndexMap : [1,2,1,5,0,0,0,0,0]
	const binIndexArr = itemColorsAndIndices.index;
	const binIndexMap = new Array(buckets.length).fill(0);
	const binColorArr = new Array(buckets.length).fill(new Array());

	binIndexArr.forEach((e, whichRow) => {
		binIndexMap[e] += 1;
		binColorArr[e].push(itemColorsAndIndices.color[whichRow]);
	});

	const arrow_div = svg.selectAll("line.arrow");
	// Drawing the arrows for each bucket

	binIndexMap.forEach((itemIndex, whichRow) => {
		for (let j = 0; j < itemIndex; j++) {
			arrow_div
				.data(
					buckets.filter(function (d, i) {
						return i === whichRow;
					})
				)
				.enter()
				.append("line")
				.attr("x1", function (d) {
					return x(d.offset) + j * 1;
				})
				.attr("y1", height)
				.attr("x2", function (d) {
					return x(d.offset) + j * 1;
				})
				.attr("y2", function (d) {
					return y(d.height);
				})
				.style("stroke-width", 1)
				.style("stroke", binColorArr[whichRow][j])
				.style("fill", "none");
		}


		svg
			.selectAll("rect")
			.transition()
			.attr("y", function (d) {
				return y(d.height);
			})
			.attr("height", function (d) {
				return height - y(d.height);
			})
			.duration(1200)
			.delay(function (d, i) {
				return i * 50;
			});

		svg
			.append("g")
			.attr("class", "axis")
			.attr("transform", "translate(0," + height + ")")
			.call(xAxis);
	});
}

function createHeaderDistribution(descriptor, itemColorsAndIndices, index) {
	if (
		!descriptor ||
		!descriptor.histogram ||
		!descriptor.bucketName ||
		!itemColorsAndIndices
	) {
		// Missing information
		return;
	}

	if (
		descriptor.histogram.length === descriptor.bucketName.length
	) {
		createOrdinalChart(descriptor, itemColorsAndIndices, index);
	} else {
		createNumericalChart(descriptor, itemColorsAndIndices, index);
	}
}


export {
	createHeaderDistribution,
};