      <v-hover>
        <v-card top slot-scope="{ hover }">
          <v-img :src="v1" :aspect-ratio="16/9" contain class="grey lighten-2">
            <v-expand-transition>
              <v-img v-if="hover" :src="{v1}" class="transition-ease-in"></v-img>
            </v-expand-transition>
