        </v-card>
      </v-hover>
    </v-flex>-->
    <v-flex v-for="(v,i) in values" :key="i" v-bind="{[`xs${cardSize}`]: true}">
      <p v-if="!v" class="font-weight-black">NULL</p>
      <v-hover v-else>
        <v-card top slot-scope="{ hover }">
          <v-img :src="v" :aspect-ratio="16/9" class="grey lighten-2">
            <v-expand-transition>
              <v-img v-if="hover" :src="{v}" class="transition-ease-in"></v-img>
