<template>
  <v-btn style="border-color: grey" depressed small :color="`${color}`" icon></v-btn>
</template>

<script>
import * as d3 from "d3";

export default {
  /*
  Comparator component expects 2 props:
    itemsDiff: an array of the difference for each row (the difference is calculated from customized comparator in this case)
    gradient: an array of the colors being, the first item will not actually be shown since it is "to right" in this case.
*/

  name: "Comparator",
  props: ["itemsDiff", "gradient"],
  data() {
    return {
      color: null // the rgb value that is actually going to show after interpolatation
    };
  },
  computed: {
    // stores the color-encoding hex value
    colors() {
      const colors = this.gradient.split(", ");
      colors.shift(); // The first index is not an actual color
      return colors;
    }
  },
  watch: {
    itemsDiff: {
      handler() {
        // Initialize the colors array
        this.color = d3.interpolateRgbBasis(this.colors)(this.itemsDiff);
      },
      immediate: true
    }
  }
};
</script>

<style scoped>
.v-btn:hover:before,
.v-btn:focus:before {
  background-color: transparent;
}
</style>
