<template>
  <v-layout row>
    <v-flex v-for="(v,i) in values" :key="i" v-bind="{[`xs${cardSize}`]: true}">
      <a :href="{v}">{{v}}</a>
      <br />
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: "urlComparator",
  props: ["values"],
  computed: {
    cardSize() {
      return Math.floor(12 / this.values.length);
    }
  }
};
</script>

<style scoped>
a {
  word-break: break-word;
}
</style>
