<template>
  <v-layout row>
    <v-flex
      v-for="(v,i) in values"
      :key="i"
      v-bind="{[`xs${cardSize}`]: true}"
      :class="i === curIndex ? 'blue--text' : null"
    >
      <p v-if="v">{{v}}</p>
      <p v-else class="font-weight-black">NULL</p>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  name: "normalComparator",
  props: ["values", "curIndex"],
  computed: {
    cardSize() {
      return Math.floor(12 / this.values.length);
    }
  }
};
</script>

<style scoped>
p {
  word-break: break-word;
}
</style>