<template>
  <v-layout column>
    <v-expansion-panel v-for="(v,i) in values" :key="i">
      <v-expansion-panel-content>
        <template v-slot:header>
          <strong
            class="text-truncate"
            :class="curIndex === i ? 'blue--text' : null"
            v-bind:style="{
              'margin-left': `${i * (100 / values.length) }%`,
              'max-width': '150px',
              'padding': 0,
            }"
          >{{ v === null ? 'NULL' : v}}</strong>
        </template>
        <v-card>
          <v-card-text>{{v}}</v-card-text>
        </v-card>
      </v-expansion-panel-content>
    </v-expansion-panel>
    <!-- </v-flex> -->
  </v-layout>
</template>

<script>
export default {
  name: "longTextComparator",
  props: ["values", "curIndex"]
};
</script>

<style>
.v-expansion-panel__header {
  padding: 0;
}
</style>