const StringComparator = (valueA, valueB) => {
  if (
    (valueA === undefined || valueA === null) &&
    (valueB === undefined || valueB === null)
  )
    return 0;

  if (valueA === undefined || valueA === null) return 0;
  if (valueB === undefined || valueB === null) return 0;

  if (typeof valueA === "string" && typeof valueB === "string") {
    return valueA.localeCompare(valueB);
  } else {
    return 0;
  }
};

const NumberComparator = (valueA, valueB) => {
  if (
    (valueA === undefined || valueA === null) &&
    (valueB === undefined || valueB === null)
  )
    return 0;

  if (valueA === undefined || valueA === null) return 0;
  if (valueB === undefined || valueB === null) return 0;
  if (typeof valueA === "number" && typeof valueB === "number") {
    return (valueA - valueB) / (valueA + valueB);
  } else {
    return 0;
  }

  // if (typeof valueA == "string" && typeof valueB == "string")
  //   val = valueA.length - valueB.length;
  // else if (typeof valueA == "number" && typeof valueB == "number")
  //   val = valueA - valueB;
  // else val = valueA - valueB;
  // return val;
};

export {
  StringComparator,
  NumberComparator
};